import React from 'react';
import { connect }      from 'react-redux';

class ExpenseForm extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            CustomerId: props.users.userId,
            PizzaType: 0,
            ToppingType: 0,
            Status: 1,
            error: !props.users.loggedIn ? 'First you need to login' : ''
       }
    };
    onSubmit = (e) => {
        e.preventDefault();
        if(this.state.PizzaType === 0) {
            this.setState(() => ({
                error: 'Please choose pizza type'
            }));
        } else if(this.state.ToppingType === 0) {
            this.setState(() => ({
                error: 'Please choose pizza type'
            }));
        } else {
            this.setState(() => ({
                error: ''
            }));
        this.props.onSubmit({
            description: this.state.description,
            amount: parseFloat(this.state.amount) * 100,
            createdAt: this.state.createdAt.valueOf(),
            note: this.state.note
        });
        }
    };

   render(){
       return (
           <div>
               {this.state.error !== '' && <p>{this.state.error}</p>}
               <form onSubmit={this.onSubmit}>
                   <input 
                       type="text" 
                       placeholder="Description"
                       autoFocus
                       value={this.state.description}
                       onChange={this.onDescriptionChange}
                   />
                   <input 
                       type="text"
                       placeholder="Amount"
                       value={this.state.amount}
                       onChange={this.onAmountChange}
                   />
                   <SingleDatePicker 
                       date={this.state.createdAt}
                       onDateChange={this.onDateChange}
                       focused={this.state.calendarFocused}
                       onFocusChange={this.onFocuseChage}
                       id="ExpanseFormDatePicker"
                       numberOfMonths={3}
                       isOutsideRange={() => false}
                   />
                   <textarea 
                       placeholder="Add a note for your expense"
                       value={this.state.note}
                       onChange={this.onNoteTextChage}
                   >
                   </textarea>
                   <button>Add expense</button>
               </form>
           </div>
       );
   };
}

const mapStateToPorps = (state) => {
    return {
        orders: state.orders,
        users: state.users
    };
};

export default connect(mapStateToPorps)(ExpenseForm);


